	background: no-repeat url(/static/media/face_co.a04db5f6.png); 
	background-size: 100%; 
	-webkit-transform-origin: center bottom;
	transform-origin: center bottom;
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	transition: 1s -webkit-transform;
	transition: 1s transform;
	transition: 1s transform, 1s -webkit-transform;
}

.about-description > .pp > figure > figcaption { 
	width: 100%;
	height: 50px;
	background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), black;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: bottom;
	color: #fff; 
	position: relative;
	top: 300px;
	-webkit-transform-origin: center top;
	transform-origin: center top;
	-webkit-transform: rotateX(-89.9deg);
	transform: rotateX(-89.9deg);
	font-size: 18px;
	font-family: Montserrat, Arial, sans-serif;
	text-align: center;
	line-height: 3;
}

.pp > figure:before {
	content: '';
	position: absolute;
	top: 0; 
	left: 0;
	width: 100%;
	height: 100%;
	box-shadow: 0 0 100px 50px rgba(0, 0, 0, 0.1), inset 0 0 250px 250px rgba(0, 0, 0, 0.1);
	transition: 1s;
	-webkit-transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	-webkit-transform-origin: inherit;
	transform-origin: inherit;
}

.pp:hover > figure { 
	-webkit-transform: rotateX(75deg) translateZ(5vw); 
	transform: rotateX(75deg) translateZ(5vw); 
}

.pp:hover > figure:before {
	box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.5), inset 0 0 250px 250px rgba(0, 0, 0, 0.5);
	-webkit-transform: rotateX(-5deg) translateZ(-80px) scale(1);
	transform: rotateX(-5deg) translateZ(-80px) scale(1);
}

