.modal-container {
	width: 100vw;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	background-color: rgba(0, 0, 0, .7);
	z-index: 9999;
	-webkit-animation-name: grow-modal;
	animation-name: grow-modal;
	-webkit-animation-duration: .3s;
	animation-duration: .3s;
	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;
}

@-webkit-keyframes grow-modal {
0% { opacity: .2; }
25% { opacity: .4; }
50% { opacity: .6; }
75% { opacity: .8; }
100% { opacity: .9; }
}

@keyframes grow-modal {
0% { opacity: .2; }
25% { opacity: .4; }
50% { opacity: .6; }
75% { opacity: .8; }
100% { opacity: .9; }
}

.modal {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.modal-content {
	width: 100%;
	margin: 50px 200px;
	background-color: #fff;
	-webkit-animation-name: grow-modal-content;
	animation-name: grow-modal-content;
	-webkit-animation-duration: .3s;
	animation-duration: .3s;
	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;
}

@-webkit-keyframes grow-modal-content {
0% { width: 20%;}
100% { width: 100%; }
}

@keyframes grow-modal-content {
0% { width: 20%;}
100% { width: 100%; }
}

.modal-header,
.modal-footer {
	padding: 10px;
}

