#loader {
	height: calc(100vh - 100px);
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

#square {
	width: 100px;
	height: 100px;
	background-color: #24283b;
}

#final-square {
	position: absolute;
	top: calc(50% - 64px);
	left: calc(50% - 35px);
	width: 100px;
	height: 100px;
	border: 6px solid #eb6f92;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	-webkit-animation: final 5s linear 1;
	animation: final 5s linear 1;
}

#top {
	position: relative;
	top: 50%;
	left: 50%;
	width: 100px;
	height: 2px;
	background-color: #ebbcba;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

#right {
	position: relative;
	top: calc(50% + 68px);
	left: 50%;
	width: 100px;
	height: 2px;
	background-color: #ebbcba;
	-webkit-transform: rotate(135deg);
	transform: rotate(135deg);
}

#bottom {
	position: relative;
	top: calc(50% + 66px);
	left: calc(50% - 70px);
	width: 100px;
	height: 2px;
	background-color: #ebbcba;
	-webkit-transform: rotate(-135deg);
	transform: rotate(-135deg);
}

#left {
	position: relative;
	top: calc(50% - 6px);
	left: calc(50% - 70px);
	width: 100px;
	height: 2px;
	background-color: #ebbcba;
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

#top::before,
#right::before,
#bottom::before,
#left::before {
	position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	background-color: #24283b;
	-webkit-animation: animate 5s ease-in-out 1;
	animation: animate 5s ease-in-out 1;
}

#top::after {
	content: 'FS';
	position: absolute;
	width: 100%;
	height: 100%;
	margin-top: 16px;
	margin-left: 14px;
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
	-webkit-animation: text 5s linear 1;
	animation: text 5s linear 1;
	font-size: 30px;
	font-weight: bold;
}

@-webkit-keyframes final {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	} 
}

@keyframes final {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	} 
}

@-webkit-keyframes text {
	0% {
		opacity: 0;
	}
	30% {
		opacity: 0.10;
	}
	50% {
		opacity: 0.20;
	}
	75% {
		opacity: 0.40;
	}
	85% {
		opacity: 0.60;
	}
	100% {
		opacity: 1;
	}
}

@keyframes text {
	0% {
		opacity: 0;
	}
	30% {
		opacity: 0.10;
	}
	50% {
		opacity: 0.20;
	}
	75% {
		opacity: 0.40;
	}
	85% {
		opacity: 0.60;
	}
	100% {
		opacity: 1;
	}
}

@-webkit-keyframes animate {
	0% {
		left: 0;
	}
	100% {
		left: 100%;
	}
}

@keyframes animate {
	0% {
		left: 0;
	}
	100% {
		left: 100%;
	}
}

*, *::before, *::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

