#animated {
	opacity: 0;
	-webkit-transition: opacity 1.2s ease-in-out;
	-moz-transition: opacity 1.2s ease-in-out;
	-ms-transition: opacity 1.2s ease-in-out;
	transition: opacity 1.2s ease-in-out;
	height: 0px; /* Needed to load starting animation */
	overflow: hidden;
	/* We set rules in JS after loading starting animation */
	/* opacity: 1; height: 100vh; padding-top: 100px; */
}

.content {
	display: flex;
	flex-direction: row;
	height: 100%;
}

main {
	width: 1000px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	font-size: 50px;
	-ms-overflow-style: none; /* not display scrollbar for IE and Edge */
	scrollbar-width: none; /* not display scrollbar for Firefox */
	overflow-y: scroll;
	scroll-behavior: smooth;
}

main::-webkit-scrollbar {
    display: none; /* not display scrollbar for Chrome, Safari and Opera */
}

.scroll {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	margin-bottom: 30px;
	margin-left: 10px;
}

/* For smartphones */
@media only screen and (max-width: 414px) {
	.scroll {
		display: none;
	}
}

