#about {
	display: flex;
	flex-direction: column;
}

.about-description {
	display: flex;
	flex-direction: row;
	align-items: center;
	font-size: 18px;
}

.about-description > .text {
	width: 650px;
	margin-right: 30px;
}

.about-description > .text > p {
	padding-bottom: 20px;
	text-align: justify;
	text-indent: 40px;
	line-height: 24px;
}

.about-description > .text > p > span,
.about-description > .text > p > a {
	color: #eb6f92;
}

.about-description > .text > p > .full-name {
	font-weight: bold;
	color: #ebbcba;
}

.about-description > .text > p > a {
	-webkit-transition: box-shadow 0.3s;
	-moz-transition: box-shadow 0.3s;
	-o-transition: box-shadow 0.3s;
	transition: box-shadow 0.3s;
	padding: 0px 2px 0px 2px;
}

.about-description > .text > p > a:hover {
	-webkit-box-shadow: 0px 0px 11px #ebbcba;
	-moz-box-shadow: 0px 0px 11px #ebbcba;
	box-shadow: 0px 0px 11px #ebbcba;
	border-radius: 4px;
	-webkit-transition: box-shadow 0.3s;
	-moz-transition: box-shadow 0.3s;
	-o-transition: box-shadow 0.3s;
	transition: box-shadow 0.3s;
	font-weight: bold;
}

.about-description > .pp {
	width: 300px;
	-webkit-perspective: 1000px;
	-moz-perspective: 1000px;
	perspective: 1000px;
	margin: 0 auto;
}

.about-description > .pp > figure {
	width: 100%;
	height: 300px;
	background: no-repeat url('../img/face_co.png');
	-webkit-background-size: 100%; 
	-moz-background-size: 100%; 
	background-size: 100%; 
	-webkit-transform-origin: center bottom;
	-moz-transform-origin: center bottom;
	-ms-transform-origin: center bottom;
	-o-transform-origin: center bottom;
	transform-origin: center bottom;
	-webkit-transform-style: preserve-3d;
	-moz-transform-style: preserve-3d;
	transform-style: preserve-3d;
	-webkit-transition: 1s transform;
	-moz-transition: 1s transform;
	-o-transition: 1s transform;
	transition: 1s transform;
}

.about-description > .pp > figure > figcaption { 
	width: 100%;
	height: 50px;
	background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), black;
	-webkit-background-size: 100%;
	-moz-background-size: 100%;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: bottom;
	color: #fff; 
	position: relative;
	top: 300px;
	-webkit-transform-origin: center top;
	-moz-transform-origin: center top;
	-ms-transform-origin: center top;
	-o-transform-origin: center top;
	transform-origin: center top;
	-webkit-transform: rotateX(-89.9deg);
	-moz-transform: rotateX(-89.9deg);
	-ms-transform: rotateX(-89.9deg);
	-o-transform: rotateX(-89.9deg);
	transform: rotateX(-89.9deg);
	font-size: 18px;
	font-family: Montserrat, Arial, sans-serif;
	text-align: center;
	line-height: 3;
}

.pp > figure:before {
	content: '';
	position: absolute;
	top: 0; 
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-box-shadow: 0 0 100px 50px rgba(0, 0, 0, 0.1), inset 0 0 250px 250px rgba(0, 0, 0, 0.1);
	-moz-box-shadow: 0 0 100px 50px rgba(0, 0, 0, 0.1), inset 0 0 250px 250px rgba(0, 0, 0, 0.1);
	box-shadow: 0 0 100px 50px rgba(0, 0, 0, 0.1), inset 0 0 250px 250px rgba(0, 0, 0, 0.1);
	-webkit-transition: 1s;
	-moz-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
	-webkit-transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	-moz-transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	-ms-transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	-o-transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	transform: rotateX(95deg) translateZ(-80px) scale(0.75);
	-webkit-transform-origin: inherit;
	-moz-transform-origin: inherit;
	-ms-transform-origin: inherit;
	-o-transform-origin: inherit;
	transform-origin: inherit;
}

.pp:hover > figure { 
	-webkit-transform: rotateX(75deg) translateZ(5vw); 
	-moz-transform: rotateX(75deg) translateZ(5vw); 
	-ms-transform: rotateX(75deg) translateZ(5vw); 
	-o-transform: rotateX(75deg) translateZ(5vw); 
	transform: rotateX(75deg) translateZ(5vw); 
}

.pp:hover > figure:before {
	-webkit-box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.5), inset 0 0 250px 250px rgba(0, 0, 0, 0.5);
	-moz-box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.5), inset 0 0 250px 250px rgba(0, 0, 0, 0.5);
	box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.5), inset 0 0 250px 250px rgba(0, 0, 0, 0.5);
	-webkit-transform: rotateX(-5deg) translateZ(-80px) scale(1);
	-moz-transform: rotateX(-5deg) translateZ(-80px) scale(1);
	-ms-transform: rotateX(-5deg) translateZ(-80px) scale(1);
	-o-transform: rotateX(-5deg) translateZ(-80px) scale(1);
	transform: rotateX(-5deg) translateZ(-80px) scale(1);
}

/* For small smartphones. */
@media only screen and (max-width: 400px) {
	#about {
		margin-top: 100px;
	}

	.pp {
		display: none;
	}

	.about-description {
		display: block;
	}

	.about-description > .text {
		width: 100%;
		padding: 0 16px;
	}

	p {
		padding-bottom: 40px !important;
	}

	.about-description > .text > p:first-child {
		padding-bottom: 10px !important;
	}

	.about-description > .text > p:first-child::after {
		content: '';
		background: no-repeat center/80% url('../img/face_co.png');
		display: block;
		margin: 0 auto;
		width: 300px;
		height: 300px;
		border-radius: 50%;
	}
}

/* For smartphones and tablets. */
@media only screen and (min-width: 401px) and (max-width: 822px) {
	.pp {
		display: none;
	}
	
	.about-description {
		display: block;
	}

	.about-description > .text {
		width: 100%;
		padding: 0 25px;
	}

	p {
		padding-bottom: 40px !important;
	}

	.about-description > .text > p:first-child {
		padding-bottom: 10px !important;
	}

	.about-description > .text > p:first-child::after {
		content: '';
		background: no-repeat center/80% url('../img/face_co.png');
		display: block;
		margin: 0 auto;
		width: 300px;
		height: 300px;
		border-radius: 50%;
	}
}
